import React, { Component } from 'react';
import Welcome from './Welcome'

class Register extends Component {
	constructor(props) {
		super(props);
		this.state = {
    //   firstName: '',
    //   lastName: '',
    //   email: '',
    //   status: 'pending',
    //   vip: false
      newMember: '',
      errorMess: ''
    }
    this.onSubmit = this.onSubmit.bind(this)
	}
	
	onSubmit = (event) => {
    event.preventDefault()
    let data = new FormData(event.target)
    if (!event.target.checkValidity()) {
      // form is invalid! so we do nothing
      console.log("Form invalid")
      return
    }  
      fetch('/api/addMember/', {
        method: 'POST',
        body: stringifyFormData(data), // data can be `string` or {object}!
        headers: {
          'Content-Type': 'application/json'
        }
      })
      .then(res => res.json())
      .then( (json) => {
        if (json.status === 400 || json.status === 404) {
          this.setState({
            newMember: null,
            errorMess: json.title
          })
          console.log("error: ", this.state.errorMess)
          } else {
          this.setState({
            newMember: json
          })
        }
      })
      .catch((err) => {
        console.log("error", err)
      })
    }
  
	render() {
  if (this.state.newMember) {
    var firstName, lastName
    firstName = this.state.newMember.merge_fields.FNAME
    lastName = this.state.newMember.merge_fields.LNAME
    console.log("Member added:", firstName + " " + lastName)
    return  <Welcome firstName={firstName} lastName={lastName} />
  } else if(this.state.errorMess) {
      return (
        <div className="alert alert-warning text-left">
          <strong>Error: {this.state.errorMess}</strong>
        </div>
      )
    } else {
      return (
          <div id="App-content">
            <div className="section App-section App-form">
              <main className="pa4 blue-80">
                <div className="measure">
                  <form onSubmit={this.onSubmit} id="sign_up" className="ba b--transparent ph0 mh0">
                    <legend className="f4 fw6 ph0 mh0">Register</legend>
                    <div className="mt3">
                      <input className="pa1 input-reset ba b--black bg-light hover-black w-100"
                        placeholder="First Name"
                        type="text"
                        name="firstName"
                        id="firstName"
                        required
                        // onChange={this.onFirstNameChange}
                      />
                    </div>
                    <div className="mt3">
                      {/* <label className="db fw6 lh-copy f6" htmlFor="lastName">Last Name</label> */}
                      <input className="pa1 input-reset ba b--black bg-light hover-black w-100"
                        placeholder="Last Name"
                        type="text"
                        name="lastName"
                        id="lastName"
                        required
                        // onChange={this.onLastNameChange}
                      />
                    </div>
                    <div className="mt3">
                      {/* <label className="db fw6 lh-copy f6" htmlFor="email-address">Email</label> */}
                      <input className="pa1 input-reset ba b--black bg-light hover-black w-100"
                        placeholder="Email address"
                        type="email"
                        name="email"
                        id="email"
                        required
                        // onChange={this.onEmailChange}
                      />
                    </div>
                    <br />
                    {/* <div className="mt3"> */}
                      <input
                        className="b ph3 pv2 input-reset ba b--black bg-primary text-white grow pointer f6 dib"
                        type="submit"
                        value="Submit"
                      />
                    {/* </div> */}
                  </form>
                </div>
              </main>
            </div>
          </div>
        )
      }
    }
  }

  export default Register;

  function stringifyFormData(fd) {
    const data = {};
    for (let key of fd.keys()) {
      data[key] = fd.get(key);
    }
    return JSON.stringify(data, null, 2);
  }